import React from 'react';
import './format.css';
import './contact.css';

class ContactForm extends React.Component {
    render() {
        return (
            <form action="https://formspree.io/xbjyvgld" method="post">
                <div class="row">
                    <div class="col-7 col-l-5 col-m-2 col-s-2 non-es-only"></div>
                    <div class="col-1 col-l-1 col-m-1 col-s-1 non-es-right-align"><label for="name">Name:</label></div>
                    <div class="col-5 col-l-5 col-m-7 col-s-3"><input type="text" name="name" style={{'width':'100%'}} /></div>
                    <div class="col-7 col-l-5 col-m-2 col-s-2 non-es-only"></div>
                </div>
                <div class="row">
                    <div class="col-7 col-l-5 col-m-2 col-s-2 non-es-only"></div>
                    <div class="col-1 col-l-1 col-m-1 col-s-1 non-es-right-align"><label for="_replyto">Email:</label></div>
                    <div class="col-5 col-l-5 col-m-7 col-s-3"><input type="email" name="_replyto" style={{'width':'100%'}} /></div>
                    <div class="col-7 col-l-5 col-m-2 col-s-2 non-es-only"></div>
                </div>
                <div class="row">
                    <div class="col-7 col-l-5 col-m-2 col-s-2 non-es-only"></div>
                    <div class="col-1 col-l-1 col-m-1 col-s-1 non-es-right-align"><label for="message">Message:</label></div>
                    <div class="col-5 col-l-5 col-m-7 col-s-3"><textarea name="message" style={{'width':'100%','height':'100%'}}></textarea></div>
                    <div class="col-7 col-l-5 col-m-2 col-s-2 non-es-only"></div>
                </div>
                <div class="row">
                    <div class="col-7 col-l-5 col-m-2 col-s-2 non-es-only"></div>
                    <div class="col-6 col-l-6 col-m-8 col-s-4"><input type="submit" value="Send" style={{'width':'100%'}} /></div>
                    <div class="col-7 col-l-5 col-m-2 col-s-2 non-es-only"></div>
                </div>
            </form>
        );
    }
}

export default ContactForm;
