import React from 'react';
import './format.css';
import './embed.css';

class Videos extends React.Component {
    render() {
        return (
            <div class="row">
                <div class="col-6 col-l-4 non-mobile-only"></div>
                <div class="col-8 col-l-8">
                    <div class="embed-responsive embed-responsive-16by9">
                        <iframe class="embed-responsive-item" src="https://www.youtube.com/embed/srmm5_qwHek" allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture" title="Look Up - Original Music" allowfullscreen>Your browser does not support IFrames.</iframe>
                    </div>
                </div>
                <div class="col-6 col-l-4 non-mobile-only"></div>
            </div>
        );
    }
}

export default Videos;
