import React from 'react';
import {Link} from 'react-router-dom';
import './format.css';
import './menu.css';

function showMenu() {
    var text;
  
    if (document.getElementById('menu-sub').style.display === 'block') {
        document.getElementById('menu-sub').style.display = 'none';
        text = document.getElementById('menu-button').textContent;
        document.getElementById('menu-button').textContent = text.slice(3, text.length - 3);
    } else {
        document.getElementById('menu-sub').style.display = 'block';
        text = document.getElementById('menu-button').textContent;
        document.getElementById('menu-button').textContent = '<< ' + text + ' >>';
    }
};

class Menu extends React.Component {
    render() {
        return (
            <div>
                <div class="row mobile-only">
                    <div class="col-"><span id="menu-button" class="nav-button" onClick={showMenu}>Menu</span></div>
                    <div id="menu-sub" style={{'display': 'none'}}>
                        <div class="col-"><Link to="/videos" class="nav-button">Videos</Link></div>
                        <div class="col-"><Link to="/bio" class="nav-button">Bio</Link></div>
                        <div class="col-"><Link to="/buy" class="nav-button">Buy</Link></div>
                        <div class="col-"><Link to="/connect" class="nav-button">Connect</Link></div>
                    </div>
                </div>
                <div class="row non-es-only">
                    <div class="col-4 col-l-2 col-m-2 col-s-2"></div>
                    <div class="col-3 col-l-3 col-m-2 col-s-1 font-8-s"><Link to="/videos" class="nav-button">Videos</Link></div>
                    <div class="col-3 col-l-3 col-m-2 col-s-1 font-8-s"><Link to="/bio" class="nav-button">Bio</Link></div>
                    <div class="col-3 col-l-3 col-m-2 col-s-1 font-8-s"><Link to="/buy" class="nav-button">Buy</Link></div>
                    <div class="col-3 col-l-3 col-m-2 col-s-1 font-7-s"><Link to="/connect" class="nav-button">Connect</Link></div>
                    <div class="col-4 col-l-2 col-m-1 col-s-2"></div>
                </div>
            </div>
        );
    }
}

export default Menu;
