import React from 'react';
import {BrowserRouter as Router, Switch, Route} from 'react-router-dom';
import Title from './title';
import Menu from './menu';
import Tagline from './tagline';
import ShortBio from './short-bio';
import Videos from './videos';
import LongBio from './longbio';
import Buy from './buy';
import ContactHeader from './contactHeader';
import ContactForm from './contactForm';
import SubscribeHeader from './subscribeHeader';
import SubscribeNote from './subscribeNote';
import SubscribeForm from './subscribeForm';

const Home = () => <div><Title /><Menu /><Tagline /><ShortBio /></div>
const VideosPage = () => <div><Title /><Menu /><Videos /></div>
const Bio = () => <div><Title /><Menu /><LongBio /></div>
const BuyPage = () => <div><Title /><Menu /><Buy /></div>
const Contact = () => <div><Title /><Menu /><ContactHeader /><ContactForm /><SubscribeHeader /><SubscribeNote /><SubscribeForm /></div>

function App() {
    return (
        <Router>
            <div>
                <Switch>
                    <Route exact path="/">
                        <Home />
                    </Route>
                    <Route path="/videos">
                        <VideosPage />
                    </Route>
                    <Route path="/bio">
                        <Bio />
                    </Route>
                    <Route path="/buy">
                        <BuyPage />
                    </Route>
                    <Route path="/connect">
                        <Contact />
                    </Route>
                </Switch>
            </div>
        </Router>
    );
}

export default App;
