import React from 'react';
import './format.css';

class LongBio extends React.Component {
    render() {
        return (
            <div class="row">
                <div class="col-4 full-only"></div>
                <div class="col-12">
                    <p>Every person is a story, and storyteller Daniel Unterholzner is no different. He has been telling stories when he was younger. He pursued and is pursuing his creative cravings through short films, acting and playing music in musical theatre, developing and critiquing video games, and writing and recording music.</p>
                    <p>Even before entering high school, Daniel participated in the a "Go Film!" workshop at the San Joaquin County Office of Education in Stockton, California learning about camera, lighting, and sound techniques. He worked with other teens to create and release a short film, as the Gaffer, the Foley Artist, and a Sound Editor. In high school, Daniel took a class expanding his film knowledge in creating the school's announcements. After graduating, he started a YouTube channel where he posted short videos.</p>
                    <p>Along with working in film, Daniel also participated in musical theatre. He appeared in many productions starting in 2007 with "Dear Edwina Jr" at Stockton Civic Theatre as well as many church musicals. In 2009, Daniel starred in "The Music Man" for a senior project in Manteca, California. Later, he was cast as Professor Quirrel and was set to be the Musical Director for a production of "A Very Potter Musical" while directing and developing a Murder Mystery Dinner containing mostly improvised scenes.</p>
                    <p>Daniel has always been interested in video game development and design as he played them often throughout his adolescence. This interest flourished and inspired him to major in Computer Science, first at San Joaquin Delta College and then University of the Pacific. He ended up switching his major to Media X to further pursue his passions. During his coursework, he worked with classmates to develop two games: a combat simulator for the tabletop role-playing game Dungeons and Dragons and a bullet-dodging game like the video game Cuphead. Daniel intends to continue using video games as an alternate form of storytelling in the future.</p>
                    <p>Music has always been part of Daniel's life. Inspired by his older sister taking lessons, he decided to learn piano at age 5. He later picked up more instruments such as the clarinet, the cello, and the guitar. Daniel was active in his high school's band program where he took to arranging and writing for his school's wind ensemble and jazz band. He also played cello often in college and assisted East Union High School's theatre class in a production of "Next to Normal." In 2019, Daniel sent a video of him playing an original song to the University of the Pacific’s student association. This resulted in Daniel playing both original and cover songs at their Brickyard Series concert. Daniel now writes indie pop music in his spare time.</p>
                    <p>Daniel hopes to further his storytelling career using his YouTube channel talking about gaming, music, and languages. His free time consists of learning German, American Sign Language, Spanish, and Italian. When he's not adapting The Odyssey for musical theatre, Daniel plays and runs sessions of various tabletop role-playing games for which he hopes to develop a podcast in the future.</p>
                </div>
                <div class="col-4 full-only"></div>
            </div>
        );
    }
}

export default LongBio;
